<script>
import PictureInput from "vue-picture-input";
import ModalDiv from "../../components/utils/Modal";
export default {
  name: "SignUpPage",
  data() {
    return {
      fileUploadError: "",
      stepTitle: "Account Validation",
      currentStep: "step1",
      acctInfo: {
        chargeAccount: "",
        chargeDueDate: "15",
        lastname: "",
        firstname: "",
        middlename: "",
        address: "",
        email: "",
        phone: "",
        subscriptionPLan: "1",
        sub_day: "",
        countryCode: "",
        stateCode: "",
        cityCode: "",
        solId: "",
        // branchCode: "",
        image: ""
      },
      errors: {},
      utils: {
        states: null,
        cities: null,
        branches: null
      },
      userDetails: [],
      termsAndC: false,
      firstAccount: "",
      validAcctNo: []
    };
  },
  components: {
    PictureInput,
    ModalDiv
  },
  methods: {
    addImage(args) {
      // eslint-disable-next-line
      // console.log("got it", args);
      return (this.acctInfo.image = args);
    },
    formIsInvalid(formData) {
      // To validate the  fields
      this.errors = {};
      let isEmpty;
      for (var key in formData) {
        if (key === "countryCode") {
          formData[key] = "NGN";
        }
        if (key === "middlename") {
          formData[key] = "-";
        }
        if (key === "image") {
          formData[key] = "-";
        }
        if (key === "sub_day") {
          formData[key] = this.acctInfo.subscriptionPLan;
        }
        if (formData[key] === "") {
          isEmpty = true;
          this.errors[key] = `Please ${key} cannot be empty`;
          // this.$awn.alert(`Please ${key} cannot be empty`, {
          //   labels: { alert: "Issue" }
          // });
        }
      }
      return isEmpty ? true : false;
    },
    getStates() {
      this.axios
        .get("/getstates")
        .then(response => {
          return (this.utils.states = response.data);
        })
        .catch(err => {
          return new Error(err);
        });
    },
    getCites() {
      this.axios
        .get(`/getcities?statecode=${this.acctInfo.stateCode}`)
        .then(response => {
          return (this.utils.cities = response.data);
        })
        .catch(err => {
          return new Error(err);
        });
    },
    getBranches() {
      this.axios
        .get(`/getbranches?citycode=${this.acctInfo.cityCode}`)
        .then(response => {
          return (this.utils.branches = response.data);
        })
        .catch(err => {
          return new Error(err);
        });
    },
    getUserDetails() {
      setTimeout(() => {
        return (this.userDetails = this.$store.state.USER);
      }, 1000);
    },
    validateAccount() {

        return (this.currentStep = "step2");
      // Validate the account Number and show the checked green icon  for validation
      // if (!this.firstAccount) {
      //   return (this.errors.firstAccount = `Please account cannot be empty`);
      // } else if (this.userDetails.accountNo.includes(this.firstAccount)) {
      //   this.stepTitle = "Other Information";
      //   return (this.currentStep = "step2");
      // } else {
      //   return (this.errors.firstAccount = `Please enter a valid Tier 3 Account`);
      // }
    },
    sendInfo() {
      const notValid = this.formIsInvalid(this.acctInfo);
      // eslint-disable-next-line
      if (!notValid) {
        if (!this.termsAndC) {
          return this.$awn.alert("Please accept Terms & Conditions to proceed");
        } else {
          this.axios
            .post("/subscribe", this.acctInfo)
            .then(response => {
              response;
              this.$awn.success("Submit Successful", {
                labels: {
                  success: "Processing..."
                }
              });

              this.stepTitle = "Completed";
              return (this.currentStep = "step3");
              // setTimeout(() => {
              //   this.$awn.success("An Email would be sent to you contain", {
              //     labels: {
              //       success: "Completed"
              //     }
              //   })
              // } , 3000)
            })
            .catch(err => {
              // temporary
              err;
              // this.$awn.success("Submit Successful", {
              //   labels: {
              //     success: "Processing..."
              //   }
              // });
              // this.stepTitle = "Completed";
              // Temporr
              this.$awn.alert(
                err.response.data.msg +
                  "Something went wrong, please try again later"
              );
              //return (this.currentStep = "step3");
            });
        }
      } else {
        return "";
      }
    },
    subTypePick(subType) {
      this.acctInfo.subscriptionPLan = subType;
    }
  },
  created() {
    // this.getStates();
    // this.getUserDetails();
  },
  watch: {
    "acctInfo.stateCode": {
      handler(newValue) {
        if (newValue) {
          this.acctInfo.cityCode = "";
          this.acctInfo.solId = "";
          this.getCites();
        }
      },
      deep: true
    },
    "acctInfo.cityCode": {
      handler(newValue) {
        if (newValue) {
          this.acctInfo.solId = "";
          this.getBranches();
        }
      },
      deep: true
    },
    firstAccount: {
      handler(newValue) {
        if (newValue.length === 10) {
          this.errors.firstAccount = null;
          if (this.userDetails.accountNo.includes(newValue)) {
            return (this.validAcctNo = true);
          } else {
            this.errors.firstAccount = "Please select a valid tier 3 account";
          }
        } else {
          return (this.errors.firstAccount =
            "Please select a valid tier 3 account");
        }
      },
      deep: true
    }
  }
};
</script>

